<template>
  <div id="AdvertisingReportUpload">
    <el-form :inline="true" :model="form">
      <el-form-item label="上传文件类型">
        <el-select v-model="form.type">
          <el-option
            v-for="(item, index) in acceptType"
            :key="index"
            :label="item.label"
            :value="item.label"
          >
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="国家站点">
        <el-select v-model="form.country_station" placeholder="请选择国家站点">
          <el-option
            v-for="(item, i) in country_stations"
            :key="i"
            :label="item"
            :value="country_stations[i]"
          ></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="时间">
        <el-date-picker
          value-format="yyyy-MM-dd"
          v-model="form.date"
          type="date"
          placeholder="选择日期"
        >
        </el-date-picker>
      </el-form-item>
      <el-form-item>
        <el-upload
          class="upload-demo"
          ref="upload"
          action="123"
          :auto-upload="false"
          :http-request="submitData"
        >
          <el-button slot="trigger" size="small" type="primary"
            >文件上传</el-button
          >
          <el-button
            style="margin-left: 10px;"
            size="small"
            type="success"
            @click="submitUpload"
            >确认上传</el-button
          >
        </el-upload>
      </el-form-item>
    </el-form>
    <div class="mask" v-show="uploading">
      上传中，请稍等！
    </div>
    <!-- <div>
      <input ref="files" @change="changeFile" type="file" value="文件上传" />
    </div> -->
    <!-- <div class="fileList">
      <div v-for="(item, index) in listData" :key="index">
        <div class="countryBlock">
          {{ item.country }}
        </div>
        <ul>
          <li v-for="(item_, index_) in item.listArr" :key="index_">
            {{ item_ }}
          </li>
        </ul>
      </div>
    </div> -->
  </div>
</template>

<script>
export default {
  data() {
    return {
      uploading: false,
      // 将数据转成这种格式
      listData: [
        {
          country: "英国",
          listArr: ["fileA", "fileB", "fileC", "fileD"]
        }
      ],
      acceptType: [
        {
          label: "广告报告",
          type: "xls"
        },
        {
          label: "订单报告",
          type: "txt"
        },
        {
          label: "fba",
          type: "txt"
        }
      ],
      form: {
        country_station: "",
        date: "",
        type: ""
      },
      country_stations_code: [],
      country_stations: [
        // {
        //   name: "胤佑澳洲",
        //   value: "yy_AU"
        // },
        // {
        //   name: "胤佑美国",
        //   value: "yy_US"
        // },
        // {
        //   name: "爱瑙美国",
        //   value: "an_US"
        // },
        // {
        //   name: "胤佑日本",
        //   value: "yy_JP"
        // },
        // {
        //   name: "胤佑墨西哥",
        //   value: "yy_MX"
        // },
        // {
        //   name: "胤佑加拿大",
        //   value: "yy_CA"
        // },
        // {
        //   name: "爱瑙加拿大",
        //   value: "an_CA"
        // }
      ],
      uploadFlag: false,
      curAcceptType: ""
    };
  },
  computed: {
    times() {
      if (!this.form.date) {
        return "";
      } else {
        let [year, month, day] = this.form.date.split("-");

        return year + "_" + month + "_" + day;
      }
    }
  },
  methods: {
    submitData() {},
    // uploadHttpRequest(param) {
    //   console.log("文件");
    //   console.log(param.file);
    // },
    // changeFile() {
    //   console.log(this.$refs.files.files);
    //   this.$refs.upload.submit();
    // },
    submitUpload() {
      let fileItem = this.$refs.upload.uploadFiles[0];
      console.log(fileItem);
      if (!fileItem) {
        alert("请上传文件");
        return;
      }
      let files = fileItem.raw;
      if (files && files.name) {
        this.curAcceptType = this.acceptType.find(
          i => i.label === this.form.type
        ).type;

        let regx = new RegExp("\." + this.curAcceptType, "g");
        // if (!/\.xls$/g.test(files.name) && !/\.xlsx$/g.test(files.name)) {
        //   this.$refs.upload.uploadFiles = [];
        //   alert("请上传.xls,.xlsx后缀表格文件");
        //   return;
        // }
        if (!regx.test(files.name)) {
          this.$refs.upload.uploadFiles = [];
          alert("请上传" + this.curAcceptType + "后缀文件");
          return;
        }
      }
      this.uploadFlag = true;
      if (!this.form.country_station || !this.times || !this.uploadFlag) {
        alert(
          "请选择国家站点、时间并检查上传文件类型是否为" +
            this.curAcceptType +
            "后缀文件"
        );
        return;
      }
      console.log("post请求发送");
      let formData = new FormData();
      formData.append("files", files);
      // yy_us
      let idx = this.country_stations.findIndex(
        i => i == this.form.country_station
      );
      let country_stations_code = this.country_stations_code[idx];
      console.log(country_stations_code);

      formData.append("area", country_stations_code);
      // 胤佑美国
      formData.append("area1", this.form.country_station);
      let times = this.times.split("-").join("_");
      formData.append("times", times);
      // status 报告类型
      formData.append("status", this.form.type);
      // formData.append("times", this.times);
      this.$refs.upload.submit();
      this.uploading = true;
      let url =
        this._url2 + "server/order_management/upload_advertising_report/";
      console.log(url);
      this.$axios.post(url, formData).then(res => {
        this.uploading = false;
        if (res.data.code === 200) {
          alert("上传成功！");
        } else {
          alert("上传未成功！");
        }
        console.log(this.$refs.upload);
        this.form.type = "";
        this.form.country_station = "";
        this.form.date = "";
        this.$refs.upload.uploadFiles = [];
      });
    },
    getCountryList() {
      let url = this._url2 + "server/order_management/get_upload_file_country/";
      this.$axios.get(url).then(res => {
        console.log(res);
        this.country_stations_code = res.data.site_en || [];
        this.country_stations = res.data.site || [];
      });
    }
  },
  created() {
    this.getCountryList();
  }
};
</script>

<style>
#AdvertisingReportUpload {
  position: relative;
  background: rgba(255, 255, 255, 0.9);
  width: 60%;
  margin: 22px auto;
  padding: 22px;
}
#AdvertisingReportUpload .el-upload__input {
  display: none;
}
#AdvertisingReportUpload .el-form {
  text-align: center;
  margin-top: 22px;
}
#AdvertisingReportUpload ul {
  margin-left: 100px;
  font-size: 16px;
}
#AdvertisingReportUpload li {
  line-height: 30px;
  height: 30px;
  box-sizing: border-box;
  border: 1px solid #e5e5e5;
  border-bottom: 0;
  font-size: 16px;
  padding-left: 10px;
}
#AdvertisingReportUpload li:last-child {
  border-bottom: 1px solid #e5e5e5;
}
#AdvertisingReportUpload .countryBlock {
  width: 100px;
  border: 1px solid #e5e5e5;
  float: left;
  height: 120px;
  text-align: center;
  line-height: 120px;
}
#AdvertisingReportUpload .mask {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(255, 255, 255, 0.9);
  text-align: center;
  font-size: 14px;
  line-height: 128px;
}
</style>



